<template>
  <div>
      <h2>Add Todo Item</h2>

    <div class="add">
      <form @submit="onSubmit">
          <input type="text"  v-model="title" placeholder="Add Todo..." >
          <input type="submit" value="Submit">
      </form>
    </div>
    <br>  
  </div>
</template>

<script>
import { mapActions } from 'vuex';
export default {
    name: "AddTodo",
    data() {
        return {
            title: ''
        }
    },
    methods: {
        ...mapActions(["addTodo"]),
        onSubmit(e) {
            e.preventDefault();
            this.addTodo(this.title);
            

        },

    }
}
</script>

