form {
  display: flex;
}
input[type="text"] {
  flex: 10;
  padding: 10px;
  border: 1px solid #7db46cff;
  outline: 0;
}
input[type="submit"] {
  flex: 2;
  background: #7db46cff;
  color: #fff;
  border: 3px #7db46cff solid;
  cursor: pointer;
}

